import { useNavigate } from "react-router-dom";
import { Modal } from "react-bootstrap";

interface ModalProps {
  show: any;
  setPleaseLoginModalShow: any;
  result: any;
}

const PleaseLoginModal = ({
  show,
  setPleaseLoginModalShow,
  result,
}: ModalProps) => {
  const navigate = useNavigate();

  const handleLoginClick = () => {
    navigate("/login", { state: { result } });
  };

  return (
    <Modal
      show={show}
      className="success-modal"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Body className="p-0 text-center">
        <div className="close-btn text-end">
          <button
            className="btn"
            onClick={() => setPleaseLoginModalShow(false)}
          >
            <img src="/assets/images/cross.svg" width="100%" alt="cross" />
          </button>
        </div>
        <div className="please-login p-5">
          <h3 className="text-center" style={{ padding: "10px 0 20px" }}>
            Please Login first...
          </h3>
          <button className="btn login-success" onClick={handleLoginClick}>
            Login
          </button>
        </div>
      </Modal.Body>
    </Modal>
  );
};

export default PleaseLoginModal;
