import React, { useState } from "react";
import { Container, Nav } from "react-bootstrap";

const Navbar = () => {
  const [selectValue, setSelectValue] = useState("english");

  const token = localStorage.getItem("token");

  const handleSelect = (e: React.ChangeEvent<HTMLSelectElement>) => {
    setSelectValue(e.target.value);
  };

  const image = {
    english: "/assets/images/uk-flag.png",
    spanish: "/assets/images/spain-flag.png",
    french: "/assets/images/france-flag.png",
  };

  return (
    <Container>
      <Nav
        className="justify-content-center justify-content-md-end "
        activeKey="/home"
      >
        <Nav.Item className="lang me-4 my-3 my-sm-0">
          <img
            src={
              selectValue === "english"
                ? image.english
                : selectValue === "spanish"
                ? image.spanish
                : image.french
            }
            alt="englishuk"
          />
          <select
            className="form-select lang-select"
            value={selectValue}
            onChange={handleSelect}
            aria-label="Default select example"
          >
            <option style={{ display: "block" }} value="english">
              English
            </option>
            <option value="spanish">Spanish</option>
            <option value="french">French</option>
          </select>
        </Nav.Item>

        <Nav.Item className="dashboard">
          {token ? (
            <Nav.Link
              className="btn btn-style text-white"
              href="/profile"
              eventKey="link-1"
            >
              Dashboard
            </Nav.Link>
          ) : (
            <Nav.Link
              className="btn btn-style text-white"
              href="/login"
              eventKey="link-1"
            >
              Login
            </Nav.Link>
          )}
        </Nav.Item>
      </Nav>
    </Container>
  );
};

export default Navbar;
