import { Container, Row, Col, Form } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { Formik } from "formik";
import * as Yup from "yup";

const ForgetPasswordComponent = () => {
  const navigate = useNavigate();

  const handleForgetPassword = (values: { email: string }) => {
    axios
      .patch("/api/users/forget-password", {
        email: values.email,
      })
      .then((res) => {
        localStorage.setItem("userId", res.data.userId);
        navigate("/confirm_email", { state: { email: values.email } });
      })
      .catch((err) => {
        alert(JSON.stringify(err.response.data));
      });
  };

  // Formik Validation
  const validationSchema = Yup.object().shape({
    email: Yup.string().email("Invalid email").required("Email is required"),
  });

  return (
    <div className="login confirm-email forget-password">
      <Container>
        <Row>
          <Col sm={12}>
            <div className="login-form">
              <div className="login-heading mb-5">
                <h2 className="text-center">Forget Password</h2>
                <h5 className="text-center mt-4">
                  Enter your Email to reset your password
                </h5>
              </div>
              <div className="banner-form login-form text-center">
                <Formik
                  initialValues={{ email: "" }}
                  onSubmit={(values) => handleForgetPassword(values)}
                  validationSchema={validationSchema}
                >
                  {({
                    handleSubmit,
                    handleChange,
                    values,
                    errors,
                    touched,
                  }) => (
                    <Form onSubmit={handleSubmit}>
                      <Form.Group className="mb-3">
                        <Form.Control
                          type="email"
                          name="email"
                          placeholder="Enter Email"
                          value={values.email}
                          onChange={handleChange}
                          className={
                            touched.email && errors.email
                              ? "is-invalid my-4"
                              : "my-4"
                          }
                          required
                        />
                        {touched.email && errors.email ? (
                          <div className="invalid-feedback">{errors.email}</div>
                        ) : null}
                        <button type="submit" className="verify-btn">
                          Submit
                        </button>
                      </Form.Group>
                    </Form>
                  )}
                </Formik>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default ForgetPasswordComponent;
