import { useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import CircularProgressBar from "../Blogs/circular-bar";
import PleaseLoginModal from "../Modals/pleaseLoginModal";
import axios from "axios";

interface Props {
  newsDetailData: {
    _id?: string;
    image?: string;
    title?: string;
    time?: string;
    date?: string;
    authenticityRate?: number;
    slug?: string;
    description?: string;
    userId?: string;
  };
}

const NewsDetailComponent = ({ newsDetailData }: Props) => {
  const [showPleaseLoginModal, setShowPleaseLoginModal] = useState(false);
  // Show Text
  const [showFullText, setShowFullText] = useState(false);

  // News Detail
  const newsDetail = newsDetailData;

  const authRate = newsDetail?.authenticityRate ?? 0;

  //Getting Three lines from Description
  const description =
    newsDetail?.description?.split(" ").slice(0, 30).join(" ") + "...";

  //Local Storage
  const token = localStorage.getItem("token");

  const userId = localStorage.getItem("userId");

  //show Please Login Modal
  const handleShowPleaseLoginModal = () => {
    setShowPleaseLoginModal(true);
  };
  // Save Report
  const handleSaveReport = () => {
    axios
      .patch(`/api/reports/save-report/${userId}`, {
        id: newsDetail?._id,
      })
      .then((res) => {
        window.location.reload();
      })
      .catch((err) => {
        alert(JSON.stringify(err.response.data));
      });
  };
  // Unsave Report
  const handleUnsave = () => {
    const userId = localStorage.getItem("userId");
    axios
      .patch(`/api/reports/unsave-report/${userId}`, {
        id: newsDetail?._id,
      })
      .then((res) => {
        window.location.reload();
      })
      .catch((err) => {
        alert(JSON.stringify(err.response.data));
      });
  };
  // Handle Click

  const handleClick = newsDetail.userId ? handleUnsave : handleSaveReport;

  return (
    <div className="result pt-5" style={{ paddingBottom: "120px" }}>
      <PleaseLoginModal
        result={newsDetail}
        show={showPleaseLoginModal}
        setPleaseLoginModalShow={setShowPleaseLoginModal}
      />
      <Container>
        <Row>
          <Col md="9" sm="12" className="m-auto">
            <div
              className="result-container news-detail-container mt-5"
              style={{
                borderColor: authRate <= 50 ? "#ee0000" : "#0CB727",
                overflow: "hidden",
              }}
            >
              <div className="img">
                <img src={newsDetail?.image} width="100%" alt="result" />
                <h4
                  className="btn blog-overlay"
                  style={{
                    background: authRate <= 50 ? "#E83434" : "#0CB727",
                  }}
                >
                  Fake
                </h4>
              </div>
              <div className="result-content">
                <div className="result-btns">
                  <div className="rate-view">
                    <h4
                      className="btn text-white mt-3"
                      style={{
                        background: authRate <= 50 ? "#E83434" : "#0CB727",
                      }}
                    >
                      <CircularProgressBar
                        progress={newsDetail?.authenticityRate ?? 0}
                        strokeWidth={2}
                        radius={20}
                      />
                      Fake Rate®
                    </h4>
                  </div>
                  <div className="share-report text-left text-md-end">
                    <button
                      className="btn me-3 mt-3"
                      onClick={token ? handleClick : handleShowPleaseLoginModal}
                    >
                      <i
                        className={`${
                          newsDetail.userId === userId ? "fa" : "fal"
                        } fa-heart me-2`}
                      ></i>
                      {newsDetail.userId === userId
                        ? "Unsave This Report"
                        : "Save This Report"}
                    </button>
                    <button
                      className="btn mt-3 "
                      onClick={() => {
                        const currentUrl = window.location.href;
                        navigator.clipboard.writeText(currentUrl);
                      }}
                    >
                      <i className="fa fa-share-alt me-2"></i>
                      Share result with your friends
                    </button>
                  </div>
                </div>
                <Row>
                  <Col md="12" sm="12">
                    <div className="result-text mt-5">
                      <h3>{newsDetail?.title}</h3>
                      {showFullText ? (
                        <p>{newsDetail?.description}</p>
                      ) : (
                        <p>{description}</p>
                      )}

                      {showFullText ? (
                        <p
                          onClick={() => setShowFullText(false)}
                          style={{
                            cursor: "pointer",
                            textDecoration: "underline",
                          }}
                        >
                          Read Less
                        </p>
                      ) : (
                        <p
                          onClick={() => setShowFullText(true)}
                          style={{
                            cursor: "pointer",
                            textDecoration: "underline",
                          }}
                        >
                          Read More
                        </p>
                      )}
                    </div>
                  </Col>
                </Row>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default NewsDetailComponent;
