import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import Blogs from "../Components/Blogs";
import Header from "../Components/Header";
import Navbar from "../Components/Header/navbarOther";
import NewsDetailComponent from "../Components/Result/news_detail";
import { ReportData } from "../Content/content";
import axios from "axios";

const NewsDetail = () => {
  const blogsData = ReportData();

  const [reportData, setReportData] = useState({});
  // Finding By SLug
  const { slug } = useParams();

  useEffect(() => {
    axios
      .get(`/report/${slug}`)
      .then((res) => {
        setReportData(res.data);
      })
      .catch((err) => {
        alert(JSON.stringify(err.response.data));
      });
  }, [slug]);

  return (
    <>
      <Header>
        <Navbar />
        <NewsDetailComponent newsDetailData={reportData} />
      </Header>
      <main>
        <Blogs blogsData={blogsData} />
      </main>
    </>
  );
};

export default NewsDetail;
