import React, { useEffect, useState } from "react";
import {
  Container,
  Row,
  Col,
  Form,
  InputGroup,
  FormControl,
} from "react-bootstrap";
import DatePicker from "react-datepicker";
import Blogs from "../Blogs";
import axios from "axios";

interface State {
  blogItem: {
    _id: string;
    image: string;
    title: string;
    time: string;
    date: string;
    authenticityRate: number;
    slug: string;
  }[];
}

const ProfileFilter = () => {
  const [query, setQuery] = useState("");
  const [isTyping, setIsTyping] = useState(false);
  const [dateRange, setDateRange] = useState<[Date | null, Date | null]>([
    null,
    null,
  ]);
  const [startDate, endDate] = dateRange;
  const [queryProfileData, setQueryProfileData] = useState<State>({
    blogItem: [],
  });

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
  };

  const handleOnChangeSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    setQuery(e.target.value);
    setIsTyping(true);
  };

  const handleClearClick = () => {
    setQuery("");
    setIsTyping(false);
  };

  useEffect(() => {
    const userId = localStorage.getItem("userId");

    axios
      .get(`/api/reports/get-save-report/${userId}`)
      .then((res) => {
        setQueryProfileData({
          blogItem: res.data,
        });
      })
      .catch((err) => {
        alert(JSON.stringify(err.response.data));
      });

    if (query === "") {
      setIsTyping(false);
    }
  }, [query, queryProfileData.blogItem]);

  const filteredProfileData = queryProfileData.blogItem.filter((item) => {
    if (query) {
      return item.title.toLowerCase().includes(query.toLowerCase());
    } else if (startDate && !endDate) {
      return new Date(item.date).getTime() >= startDate.getTime();
    } else if (endDate && !startDate) {
      return new Date(item.date).getTime() <= endDate.getTime();
    } else if (startDate && endDate) {
      const dataDate = new Date(item.date).getTime(); // convert data date to milliseconds
      return dataDate >= startDate.getTime() && dataDate <= endDate.getTime();
    } else {
      return item;
    }
  });

  return (
    <div>
      <Container>
        <Row>
          <Col sm={12}>
            <div className="profile-filter mt-5 pt-4">
              <div className="profile-filter-heading">
                <h2 className="text-white text-center mb-5">
                  Links verified by you
                </h2>
                <div className="profile-form">
                  <Row>
                    <Col lg="6" md="12" sm="12">
                      <div className="search">
                        <Form onSubmit={handleSubmit}>
                          <p>Search for any specific news</p>
                          <InputGroup className="mb-3">
                            <FormControl
                              type="text"
                              placeholder="Type here"
                              value={query}
                              onChange={handleOnChangeSearch}
                            />
                            {isTyping ? (
                              <button
                                className="btn"
                                type="button"
                                onClick={handleClearClick}
                              >
                                <img
                                  src="/assets/images/close-circle.png"
                                  alt="cross"
                                />
                              </button>
                            ) : (
                              <button className="btn" type="submit">
                                <img
                                  src="/assets/images/search-normal.png"
                                  alt="search"
                                />
                              </button>
                            )}
                          </InputGroup>
                        </Form>
                      </div>
                    </Col>
                    <Col lg="6" md="12" sm="12">
                      <div className="search-date">
                        <p>Filter by Date</p>
                        <span>
                          <DatePicker
                            selectsRange={true}
                            startDate={startDate}
                            endDate={endDate}
                            onChange={(update: any) => {
                              setDateRange(update);
                            }}
                            placeholderText="February 16, 2023 - February 22, 2023"
                            dateFormat="MMMM dd, yyyy"
                          />
                          <img
                            src="/assets/images/calendar.png"
                            alt="calendar"
                          />
                        </span>
                      </div>
                    </Col>
                  </Row>
                </div>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
      <Blogs blogsData={filteredProfileData} />
    </div>
  );
};

export default ProfileFilter;
