import React, { useState, useEffect } from "react";
import { Container, Row, Col, Form } from "react-bootstrap";
import { useNavigate, useLocation } from "react-router-dom";
import axios from "axios";
import { Formik } from "formik";
import * as Yup from "yup";

const ResetPasswordComponent = () => {
  const [visible, setVisible] = useState(true);

  const navigate = useNavigate();

  const userId = localStorage.getItem("userId");

  const handleLogin = (values: { password: string; password2: string }) => {
    axios
      .patch(`/api/users/reset-password/${userId}`, {
        password: values.password,
        password2: values.password2,
      })
      .then((res) => {
        localStorage.removeItem("userId");
        navigate("/login");
      })
      .catch((err) => {
        alert(JSON.stringify(err.response.data));
      });
  };

  // Formik Validation Schema
  const validationSchema = Yup.object().shape({
    password: Yup.string()
      .required("Password is required")
      .min(6, "Password must be at least 6 characters"),
    password2: Yup.string()
      .oneOf([Yup.ref("password"), undefined], "Passwords must match")
      .required("Password confirmation is required"),
  });

  // Show Success OTP Message
  const location = useLocation();
  const message = location.state?.message;
  useEffect(() => {
    const timeout = setTimeout(() => {
      setVisible(false);
    }, 5000);

    return () => clearTimeout(timeout);
  }, []);

  return (
    <div className="login reset-password">
      <Container style={{ position: "relative" }}>
        {visible && message ? (
          <div className="alert alert-success" role="alert">
            <h4 className="alert-heading">Success!</h4>
            <p>
              You have successfully verified your email. Please Reset your
              Password
            </p>
          </div>
        ) : null}
        <Row>
          <Col sm={12}>
            <div className="login-form">
              <div className="login-heading mb-5">
                <h3 className="text-center text-white"> Reset Password </h3>
                <h5 className="text-center mt-4">Enter Your new Pasword</h5>
              </div>
              <div className="banner-form login-form text-center">
                <Formik
                  initialValues={{
                    password: "",
                    password2: "",
                  }}
                  validationSchema={validationSchema}
                  onSubmit={(values) => handleLogin(values)}
                >
                  {({
                    handleSubmit,
                    handleChange,
                    values,
                    errors,
                    touched,
                  }) => (
                    <Form onSubmit={handleSubmit}>
                      <Form.Group className="mb-3">
                        <Form.Control
                          type="password"
                          name="password"
                          value={values.password}
                          onChange={handleChange}
                          placeholder="Enter Password"
                          className={
                            touched.password && errors.password
                              ? "is-invalid"
                              : ""
                          }
                        />
                        {touched.password && errors.password ? (
                          <div className="invalid-feedback">
                            {errors.password}
                          </div>
                        ) : null}
                        <Form.Control
                          type="password"
                          name="password2"
                          value={values.password2}
                          onChange={handleChange}
                          placeholder="Confirm Password"
                          className={
                            touched.password2 && errors.password2
                              ? "is-invalid my-4"
                              : "my-4"
                          }
                        />
                        {touched.password2 && errors.password2 ? (
                          <div className="invalid-feedback">
                            {errors.password2}
                          </div>
                        ) : null}
                        <button type="submit" className="verify-btn">
                          Reset Password
                        </button>
                      </Form.Group>
                    </Form>
                  )}
                </Formik>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default ResetPasswordComponent;
