import { useState, useEffect } from "react";

import { Container, Row, Col } from "react-bootstrap";
import CircularProgressBar from "./circular-bar";
import { useMatch } from "react-router-dom";

interface State {
  blogItem: {
    _id: string;
    image: string;
    title: string;
    time: string;
    date: string;
    authenticityRate: number;
    slug: string;
  }[];
}

interface Props {
  blogsData: {
    _id: string;
    image: string;
    title: string;
    time: string;
    date: string;
    authenticityRate: number;
    slug: string;
  }[];
}

const Blogs = ({ blogsData }: Props) => {
  const [blogsToShow, setBlogsToShow] = useState<State>({
    blogItem: [],
  });
  const [numBlogsToShow, setNumBlogsToShow] = useState(3);

  const handleLoadMore = () => {
    if (numBlogsToShow < blogsData.length) {
      setNumBlogsToShow(numBlogsToShow + 3);
    }
  };

  useEffect(() => {
    setBlogsToShow({
      blogItem: blogsData?.slice(0, numBlogsToShow),
    });
  }, [numBlogsToShow, blogsData]);

  const match = useMatch("/profile");

  console.log(blogsToShow);

  return (
    <section className="blogs">
      <Container>
        <Row>
          <Col sm="12">
            {!match ? (
              <div className="sec-heading">
                <h3 className="text-center">
                  Join <b>135,600+</b> Users Who Care When They Are Lied To In
                  Real Time
                </h3>
              </div>
            ) : null}
          </Col>
        </Row>
        {/* {blogsToShow.blogItem ? ( */}
        <Row>
          {blogsToShow.blogItem.length > 0 &&
            blogsToShow?.blogItem?.map((item) => (
              <Col xl="4" lg="6" md="6" sm="12" key={item?._id}>
                <div className="blog-item mt-5">
                  <div className="blog-img">
                    <img src={item?.image} alt="blog" />

                    <h4
                      className="btn blog-overlay"
                      style={{
                        background:
                          item?.authenticityRate < 50 ? "#E83434" : "#219653",
                      }}
                    >
                      {item?.authenticityRate < 50 ? "Fake News" : "True News"}
                    </h4>
                  </div>
                  <div className="blog-content">
                    <div className="time mb-3">
                      <div className="hour">
                        <img
                          src="/assets/images/clock.svg"
                          className="me-1"
                          alt="clock"
                        />
                        <p>{item?.time}</p>
                      </div>
                      <div className="date">
                        <img
                          src="/assets/images/calendar.svg"
                          className="me-1"
                          alt="calendar"
                        />{" "}
                        <p>{item?.date}</p>
                      </div>
                    </div>
                    <a href={`/report/${item?.slug}`} className="my-5">
                      {item?.title}
                    </a>
                    <div className="blog-auth mt-3">
                      <div className="auth-rate">
                        <CircularProgressBar
                          progress={item?.authenticityRate}
                          strokeWidth={2}
                          radius={20}
                        />
                        <span>Fake Rate</span>
                      </div>
                      <div className="share">
                        <button
                          className="btn"
                          onClick={() => {
                            const currentUrl = window.location.host;
                            navigator.clipboard.writeText(
                              currentUrl + `/report/${item.slug}`
                            );
                          }}
                        >
                          <img src="/assets/images/share.svg" alt="share" />
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </Col>
            ))}

          <Col sm="12">
            <div className="text-center mt-5">
              <button
                className="btn btn-style load-more"
                onClick={handleLoadMore}
                style={{
                  background: match ? "#FFFFFF" : "#517AA4",
                  color: match ? "#517AA4" : "#FFFFFF",
                }}
              >
                Load More
              </button>
            </div>
          </Col>
        </Row>
        {/* ) : null} */}
        {/* // ) : (
        //   <div className="text-center mt-5">
        //     <h3>No Blogs Found</h3>
        //   </div>
        // )} */}
      </Container>
    </section>
  );
};

export default Blogs;
