import React, { useState, useCallback, useRef } from "react";
import { Container, Row, Col, Form, InputGroup } from "react-bootstrap";
import BannerModal from "../Modals/bannerModal";
import OverLoadedModal2 from "../Modals/overLoadModal2";
import ResultComponent from "../Result";
import axios from "axios";

const Banner = () => {
  const [bannerModalShow, setBannerModalShow] = useState(false);
  const [overLoadModalShow, setOverLoadModalShow] = useState(false);
  const [clickCount, setClickCount] = useState(0);
  const [showContent, setShowContent] = useState(false);
  const [resultData, setResultData] = useState({});

  const handleCount = () => {
    setClickCount(clickCount + 1);
  };

  const inputRef = useRef<HTMLInputElement>(null);

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    if (clickCount >= 2) {
      setOverLoadModalShow(true);
    } else {
      setBannerModalShow(true);
      setShowContent(false);
      setResultData({}); // clear previous result data
      const link = inputRef.current?.value;
      axios
        .post("/api/reports/create-report", {
          link: link,
        })
        .then((response) => {
          setBannerModalShow(false);
          setShowContent(true);
          setResultData(response.data);
        })
        .catch((error) => {
          setBannerModalShow(false);
          setShowContent(false);
          setOverLoadModalShow(true);
        });
    }
  };

  const handlePaste = useCallback(() => {
    navigator.clipboard.readText().then((clipText) => {
      if (inputRef.current) {
        inputRef.current.value = clipText;
      }
    });
  }, []);

  return (
    <div className="banner">
      <BannerModal
        show={bannerModalShow}
        setClickCount={setClickCount}
        setbannerModalShow={setBannerModalShow}
      />
      <OverLoadedModal2
        setClickCount={setClickCount}
        show={overLoadModalShow}
        setOverLoadModal2Show={setOverLoadModalShow}
      />

      <Container>
        <Row>
          <Col sm="12">
            <div className="banner-heading text-center">
              <h1 className="text-center">
                CheckTheFake.News<sup className="sup">®</sup>
              </h1>
              <h5 className="text-center">Top #1 Fake News Detector</h5>
            </div>
          </Col>
          <Col sm="12">
            <div className="buy-me my-5 text-center">
              <a
                href="https://www.buymeacoffee.com/checkthefake"
                target="_blank"
                rel="noreferrer"
                className="bmc-btn"
              >
                {/* <i className="fas fa-swords me-2"></i> */}
                <img
                  src="/assets/images/swords.png"
                  className="me-2"
                  width="24px"
                  alt="swords"
                />
                Help Us Fight Fake News
              </a>
            </div>
          </Col>
          <Col sm="12">
            <div className="banner-form">
              <Form onSubmit={handleSubmit}>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <InputGroup>
                    <Form.Control
                      type="text"
                      ref={inputRef}
                      placeholder="Enter the link here..."
                    />
                    <button
                      onClick={handlePaste}
                      className="paste"
                      type="button"
                    >
                      Paste
                    </button>
                  </InputGroup>
                  <button
                    className="verify-btn btn-style ms-2"
                    onClick={handleCount}
                    type="submit"
                  >
                    Verify Now
                  </button>
                </Form.Group>
              </Form>
            </div>
          </Col>
          <Col sm="12">
            <div
              className="banner-example text-center mt-4
            "
            >
              <h5 className="text-center mb-4">Examples</h5>
              <ul>
                <li>
                  https://www.cnn.com/2021/05/19/china/china-mars-landing-intl-hnk-scn/index.html
                </li>
                <li>
                  https://www.cnn.com/2021/05/19/china/china-mars-landing-intl-hnk-scn/index.html
                </li>
                <li>
                  https://www.cnn.com/2021/05/19/china/china-mars-landing-intl-hnk-scn/index.html
                </li>
                <li>
                  https://www.cnn.com/2021/05/19/china/china-mars-landing-intl-hnk-scn/index.html
                </li>
              </ul>
            </div>
          </Col>
        </Row>
      </Container>
      {showContent ? <ResultComponent result={resultData} /> : null}
    </div>
  );
};

export default Banner;
