import React, { useState, useEffect } from "react";
import { Container, Row, Col, Form } from "react-bootstrap";
import { useNavigate, useLocation } from "react-router-dom";
import axios from "axios";
import { Formik } from "formik";
import * as Yup from "yup";

const LoginComponent = () => {
  const [visible, setVisible] = useState(true);

  //Save Blog Data
  const location = useLocation();
  const data = location.state?.result;

  const handleSaveData = (id: string) => {
    axios
      .patch(`/api/reports/save-report/${id}`, { id: data._id })
      .then((res) => {
        alert("Report Saved Successfully");
      })
      .catch((err) => {
        alert(JSON.stringify(err.response.data));
      });
  };

  // const handleSave = data ? handleSaveData(res.data.user._id) : null;

  const navigate = useNavigate();
  // Handle Login
  const handleLogin = (values: { email: string; password: string }) => {
    axios
      .post("/api/users/login", {
        email: values.email,
        password: values.password,
      })
      .then((res) => {
        localStorage.setItem("token", res.data.user.token);
        localStorage.setItem("userId", res.data.user._id);
        if (data) {
          handleSaveData(res.data.user._id);
          navigate("/profile");
        }
        navigate("/profile");
      })
      .catch((err) => {
        alert(JSON.stringify(err.response.data));
      });
  };

  // Formik Validation
  const validationSchema = Yup.object().shape({
    email: Yup.string().email("Invalid email").required("Email is required"),
    password: Yup.string()
      .required("Password is required")
      .min(6, "Password must be at least 6 characters"),
  });

  // Show Success OTP Message
  const message = location.state?.message;
  useEffect(() => {
    const timeout = setTimeout(() => {
      setVisible(false);
    }, 5000);

    return () => clearTimeout(timeout);
  }, []);

  return (
    <div className="login">
      <Container style={{ position: "relative" }}>
        {visible && message ? (
          <div className="alert alert-success" role="alert">
            <h4 className="alert-heading">Success!</h4>
            <p>
              You have successfully verified your email. Please login to
              continue
            </p>
          </div>
        ) : null}
        <Row>
          <Col sm={12}>
            <div className="login-form">
              <div className="login-heading mb-5">
                <h2 className="text-center">Login or Sign Up </h2>
                <h5 className="text-center mt-4">
                  You will receive an email with a link to login
                </h5>
              </div>
              <div className="banner-form login-form text-center">
                <Formik
                  initialValues={{
                    email: "",
                    password: "",
                  }}
                  validationSchema={validationSchema}
                  onSubmit={(values) => handleLogin(values)}
                >
                  {({
                    handleSubmit,
                    handleChange,
                    values,
                    errors,
                    touched,
                  }) => (
                    <Form onSubmit={handleSubmit}>
                      <Form.Group className="mb-3">
                        <Form.Control
                          type="email"
                          name="email"
                          value={values.email}
                          onChange={handleChange}
                          placeholder="Enter email"
                          className={
                            touched.email && errors.email ? "is-invalid" : ""
                          }
                        />
                        {touched.email && errors.email ? (
                          <div className="invalid-feedback">{errors.email}</div>
                        ) : null}
                        <Form.Control
                          type="password"
                          name="password"
                          value={values.password}
                          onChange={handleChange}
                          placeholder="Enter Password"
                          className={
                            touched.password && errors.password
                              ? "is-invalid my-4"
                              : "my-4"
                          }
                        />
                        {touched.password && errors.password ? (
                          <div className="invalid-feedback">
                            {errors.password}
                          </div>
                        ) : null}
                        <div className="account mb-3">
                          <a href="/forget-password">Forgot Password?</a>
                        </div>
                        <div className="account mb-4">
                          <a href="/signup">Don't Have an account?</a>
                        </div>
                        <button type="submit" className="verify-btn">
                          Login
                        </button>
                      </Form.Group>
                    </Form>
                  )}
                </Formik>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default LoginComponent;
