import { useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import CircularProgressBar from "../Blogs/circular-bar";
import PleaseLoginModal from "../Modals/pleaseLoginModal";
import axios from "axios";

interface Props {
  result: any;
}

const ResultComponent = ({ result }: Props) => {
  const [showPleaseLoginModal, setShowPleaseLoginModal] = useState(false);
  // Show Text
  const [showFullText, setShowFullText] = useState(false);

  const handleShowPleaseLoginModal = () => {
    setShowPleaseLoginModal(true);
  };

  //Getting Three lines from Description
  const description =
    result?.description?.split(" ").slice(0, 30).join(" ") + "...";

  // Local Storage
  const token = localStorage.getItem("token");
  const userId = localStorage.getItem("userId");

  // Save Report
  const handleSaveReport = () => {
    axios
      .patch(`/api/reports/save-report/${userId}`, {
        id: result?._id,
      })
      .then((res) => {
        window.location.reload();
      })
      .catch((err) => {
        alert(JSON.stringify(err.response.data));
      });
  };

  return (
    <div className="result">
      <PleaseLoginModal
        result={result}
        show={showPleaseLoginModal}
        setPleaseLoginModalShow={setShowPleaseLoginModal}
      />
      <Container>
        <Row>
          <Col md="9" sm="12" className="m-auto">
            <div className="result-container">
              <div className="img">
                <img src={result?.image} width="100%" alt="result" />
                <h4
                  className="btn blog-overlay"
                  style={{
                    background: "#E83434",
                  }}
                >
                  Fake
                </h4>
              </div>
              <div className="result-content">
                <div className="result-btns">
                  <div className="rate-view">
                    <h4
                      className="btn text-white mt-3"
                      style={{ background: "#E83434" }}
                    >
                      <CircularProgressBar
                        progress={result?.authenticityRate}
                        strokeWidth={2}
                        radius={20}
                      />
                      Fake Rate®
                    </h4>
                  </div>
                  <div className="share-report">
                    <button
                      className="btn me-3 mt-3"
                      onClick={
                        token ? handleSaveReport : handleShowPleaseLoginModal
                      }
                    >
                      <i className="fal fa-heart me-2"></i>
                      Save This Report
                    </button>
                    <button
                      className="btn mt-3"
                      onClick={() => {
                        const currentUrl = window.location.host;
                        navigator.clipboard.writeText(
                          currentUrl + `/report/${result?.slug}`
                        );
                      }}
                    >
                      <i className="fa fa-share-alt me-2"></i>
                      Share result with your friends
                    </button>
                  </div>
                </div>
                <Row>
                  <Col md="12" sm="12">
                    <div className="result-text mt-5">
                      <h3>{result?.title}</h3>
                      {showFullText ? (
                        <p>{result?.description}</p>
                      ) : (
                        <p>{description}</p>
                      )}
                      {showFullText ? (
                        <p
                          onClick={() => setShowFullText(false)}
                          style={{
                            cursor: "pointer",
                            textDecoration: "underline",
                          }}
                        >
                          Read Less
                        </p>
                      ) : (
                        <p
                          onClick={() => setShowFullText(true)}
                          style={{
                            cursor: "pointer",
                            textDecoration: "underline",
                          }}
                        >
                          Read More
                        </p>
                      )}
                    </div>
                  </Col>
                </Row>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default ResultComponent;
