import { useState, useEffect } from "react";
import Banner from "../Components/Banner";
import Blogs from "../Components/Blogs";
import Header from "../Components/Header";
import Navbar from "../Components/Header/navbarHome";
import { ReportData } from "../Content/content";
import axios from "axios";

interface Props {
  blogsData: {
    _id: string;
    image: string;
    title: string;
    time: string;
    date: string;
    authenticityRate: number;
    slug: string;
  }[];
}

const Home = () => {
  // const reportData = ReportData();
  const [reportData, setReportData] = useState<Props>({
    blogsData: [],
  });

  useEffect(() => {
    axios.get("/api/reports/get-reports").then((res) => {
      setReportData({ ...reportData, blogsData: res.data });
    });
  }, []);

  return (
    <>
      <Header>
        <Navbar />
        <Banner />
      </Header>
      <main>
        <Blogs blogsData={reportData.blogsData} />
      </main>
    </>
  );
};

export default Home;
