import { useState } from "react";
import { Container, Row, Col, Form } from "react-bootstrap";
import { useNavigate, useLocation } from "react-router-dom";
import axios from "axios";

const ConfirmEmailComponent = () => {
  const [otp, setOtp] = useState("");
  const location = useLocation();

  const formattedValue = otp
    .replace(/\D/g, "")
    .slice(0, 6)
    .split("")
    .map((digit, index) => {
      if (index === 5) {
        return digit;
      }
      return `${digit} `;
    })
    .join("");

  const handleOtpChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target;
    const digits = value.replace(/\D/g, "");
    setOtp(digits);
  };

  const navigate = useNavigate();

  const handleVerifyEmail = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    axios
      .patch("/api/users/verify-otp", { otp })
      .then((res) => {
        if (location.state?.url) {
          navigate("/login", { state: { message: res.data.message } });
        } else {
          navigate("/reset-password", { state: { message: res.data.message } });
        }
      })
      .catch((err) => {
        alert(JSON.stringify(err.response.data));
      });
  };

  return (
    <div className="login confirm-email">
      <Container>
        <Row>
          <Col sm={12}>
            <div className="login-form">
              <div className="login-heading mb-5">
                <h2 className="text-center">Confirm Email</h2>
                <h5 className="text-center mt-4">
                  You have receive an OTP on your email please enter it here
                </h5>
              </div>
              <div className="banner-form login-form text-center">
                <Form onSubmit={handleVerifyEmail}>
                  <Form.Group className="mb-3">
                    <Form.Control
                      type="text"
                      name="otp"
                      placeholder="5 4 3 2 1 4"
                      value={formattedValue}
                      className="my-4"
                      onChange={handleOtpChange}
                      pattern="[0-9]{1} [0-9]{1} [0-9]{1} [0-9]{1} [0-9]{1} [0-9]{1}"
                      maxLength={11}
                      required
                    />
                    <button type="submit" className="verify-btn">
                      Verify Email
                    </button>
                  </Form.Group>
                </Form>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default ConfirmEmailComponent;
