import { useState, useEffect } from "react";
import axios from "axios";

export const ReportData = () => {
  const [reportData, setReportData] = useState([]);
  useEffect(() => {
    axios.get("/api/reports/get-reports").then((res) => {
      setReportData(res.data);
    });
  }, [reportData]);

  return reportData;
};

