import React, { useState } from "react";
import { Container, Nav } from "react-bootstrap";
import { useMatch, useNavigate } from "react-router-dom";
import axios from "axios";

const Navbar = () => {
  const match = useMatch("/login");
  const match2 = useMatch("/confirm_email");
  const match3 = useMatch("/profile");

  const [selectValue, setSelectValue] = useState("english");

  const handleSelect = (e: React.ChangeEvent<HTMLSelectElement>) => {
    setSelectValue(e.target.value);
  };

  const image = {
    english: "/assets/images/uk-flag.png",
    spanish: "/assets/images/spain-flag.png",
    french: "/assets/images/france-flag.png",
  };

  const token = localStorage.getItem("token");

  const navigate = useNavigate();

  const handleLogout = () => {
    const id = localStorage.getItem("userId");

    axios
      .patch(`/api/users/logout/${id}`)
      .then((res) => {
        console.log(res.data);
        localStorage.removeItem("token");
        localStorage.removeItem("userId");
        navigate("/");
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <Container>
      <Nav
        className={`${
          match && match2 ? "" : "align-items-start align-items-sm-center"
        }`}
        activeKey="/home"
      >
        <Nav.Item>
          <Nav.Link
            className="btn btn-style text-white"
            href="/"
            eventKey="link-1"
          >
            Go Back To Home
          </Nav.Link>
        </Nav.Item>
        <div className="nav-item-right d-flex ms-auto">
          {/* {match3 ? (
            <Nav.Item className="text-white align-self-center me-4">
              <i className="fas fa-user me-2"></i>
              Jon Doe
            </Nav.Item>
          ) : null} */}
          <Nav.Item
            className={`lang me-4 my-3 my-sm-0 ${
              match && match2 ? "mt-auto" : "mt-0"
            }`}
          >
            <img
              src={
                selectValue === "english"
                  ? image.english
                  : selectValue === "spanish"
                  ? image.spanish
                  : image.french
              }
              alt="englishuk"
            />
            <select
              className="form-select lang-select"
              value={selectValue}
              onChange={handleSelect}
              aria-label="Default select example"
            >
              <option style={{ display: "block" }} value="english">
                English
              </option>
              <option value="spanish">Spanish</option>
              <option value="french">French</option>
            </select>
          </Nav.Item>
          {!match && !match2 && !match3 ? (
            <Nav.Item>
              {token ? (
                <Nav.Link
                  className="btn btn-style text-white"
                  href="/profile"
                  eventKey="link-1"
                >
                  Dashboard
                </Nav.Link>
              ) : (
                <Nav.Link
                  className="btn btn-style text-white"
                  href="/login"
                  eventKey="link-1"
                >
                  Login
                </Nav.Link>
              )}
            </Nav.Item>
          ) : null}
          {match3 ? (
            <Nav.Item>
              <button
                className="btn btn-style text-white logout"
                onClick={handleLogout}
              >
                Logout
              </button>
            </Nav.Item>
          ) : null}
        </div>
      </Nav>
    </Container>
  );
};

export default Navbar;
